import classes from './RoomItem.module.scss'
import { Close } from '@mui/icons-material'
import DisplayBlock from '../UI/DisplayBlock'
import RoomDetailModal from '../components/RoomDetailModal'
import { useDispatch } from 'react-redux'
import Swal from 'sweetalert2'
import { Button } from '@mui/material'
import { useState } from 'react'
import { getOneInfo, deleteOne } from '../store/room-slice'

const RoomItem = (props) => {
  const dispatch = useDispatch()
  const {
    id,
    area,
    cover,
    kind,
    name,
    price,
    region,
    section,
    shape,
    comment,
    extraExpenses,
    isAllMet,
  } = props.data
  const [isModalShown, setIsModalShown] = useState(false)
  const showModalHandler = async () => {
    await dispatch(getOneInfo({ id }))
    setIsModalShown(true)
  }
  const closeModalHandler = () => {
    setIsModalShown(false)
  }

  const deleteRoomHandler = async () => {
    let result = await Swal.fire({
      title: '',
      text: '',
      icon: 'warning',
      showCancelButton: true,
      cancelButtonText: '',
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: '',
    })
    if (result.isConfirmed) {
      dispatch(deleteOne({id}))
    }
  }
  return (
    <>
      <div className={classes.roomItem}>
        <div className={classes.pointer}>
          <Close onClick={deleteRoomHandler} />
        </div>
        <h3 className={classes.name}>{name}</h3>
        <img src={cover} alt='cover' />
        <div className={classes.intro}>
          <DisplayBlock content={region} />
          <DisplayBlock content={section} />
          <DisplayBlock content={kind} />
          <DisplayBlock content={`${area}`} />
          <DisplayBlock content={`${price}`} />
          <DisplayBlock content={shape} />
          <Button
            disableRipple
            variant='contained'
            sx={{
              margin: 1,
              cursor: 'default',
              '&:hover': { backgroundColor: '#ED6D03' },
            }}
            color='warning'
          >{` ${
            extraExpenses === undefined
              ? '0'
              : extraExpenses === null
              ? '0'
              : extraExpenses
          }  `}</Button>
          <Button
            disableRipple
            variant='contained'
            sx={{
              margin: 1,
              cursor: 'default',
              '&:hover': {
                backgroundColor: `${
                  isAllMet === 0
                    ? '#D3302F'
                    : isAllMet === false
                    ? '#D3302F'
                    : '#2F7D32'
                }`,
              },
            }}
            color={
              isAllMet === 0
                ? 'error'
                : isAllMet === false
                ? 'error'
                : 'success'
            }
          >
            {isAllMet === 0
              ? ''
              : isAllMet === false
              ? ''
              : ''}
          </Button>
        </div>
        <p>{comment === '' ? '' : comment}</p>
        <Button onClick={showModalHandler}></Button>
      </div>
      {isModalShown === true && (
        <RoomDetailModal
          isModalShown={isModalShown}
          closeModal={closeModalHandler}
        />
      )}
    </>
  )
}

export default RoomItem
